<template>
  <v-dialog v-model="showSettingDialog" :fullscreen="isMobile" max-width="600px" scrollable>
    <v-card>
      <v-card-title>
        {{ $t('top.setting') }}
      </v-card-title>
      <v-card-text class="py-0">
        <validation-observer ref="observer" v-slot="">
          <form>
            <v-row>
              <v-col cols="12">
                <div class="text-subtitle-1">{{ $t('setting.dialog.opacity.title') }}</div>
                <v-slider
                  v-model="lazyOpacity"
                  max="1"
                  min="0.1"
                  step="0.01"
                  :label="$t('setting.dialog.opacity.slider')"
                  :hint="$t('setting.dialog.opacity.hint')"
                  thumb-label
                >
                  <template v-slot:append>
                    <v-text-field v-model="lazyOpacity" class="mt-0 pt-0" type="number" style="width: 60px" />
                  </template>
                </v-slider>
                <v-divider />
              </v-col>
              <v-col cols="12">
                <div class="text-subtitle-1">{{ $t('setting.dialog.notification.title') }}</div>
                <div>
                  <v-slider
                    v-model="lazyNotificationSetting.volume"
                    max="1"
                    min="0"
                    step="0.01"
                    :label="$t('setting.dialog.notification.volume')"
                    thumb-label
                  >
                    <template v-slot:append>
                      <v-text-field
                        v-model="lazyNotificationSetting.volume"
                        class="mt-0 pt-0"
                        type="number"
                        style="width: 60px"
                      />
                    </template>
                  </v-slider>
                </div>
                <div v-for="setting in lazyNotificationSetting.settings" :key="setting.key" class="d-flex align-center">
                  <v-row>
                    <v-col class="col-sm-4 col-6">
                      <v-checkbox
                        v-model="setting.enabled"
                        :label="$t(`setting.dialog.notification.enabled.${setting.key}`)"
                      />
                    </v-col>
                    <v-col class="col-sm-3 col-6">
                      <validation-provider
                        v-if="setting.hasBefore"
                        v-slot="{ errors }"
                        :name="`${setting.key}-before`"
                        rules="required|max_value:20|min_value:0"
                      >
                        <v-text-field
                          v-model="setting.before"
                          :label="$t('setting.dialog.notification.before')"
                          :suffix="$t('setting.dialog.notification.unit')"
                          min="0"
                          max="20"
                          step="1"
                          type="number"
                          prepend-icon="mdi-alarm-note"
                          :error-messages="errors"
                          required
                        />
                      </validation-provider>
                    </v-col>
                    <v-col class="col-sm-5 col-12 d-flex align-center">
                      <v-select
                        v-model="setting.sound"
                        :items="NOTIFICATION_SOUNDS"
                        item-text="name_chs"
                        item-value="key"
                        :label="''"
                      />
                      <v-btn icon @click="playSound(setting.sound)">
                        <v-icon>mdi-play</v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                </div>
                <v-divider />
              </v-col>
              <v-col cols="12">
                <div class="text-subtitle-1">{{ $t('setting.dialog.detailArrangement.title') }}</div>
                <v-row>
                  <v-col cols="6">
                    <v-card rounded color="#00695c66" class="pa-2">
                      <v-card-title>{{ $t('setting.dialog.detailArrangement.enabled') }}</v-card-title>
                      <draggable v-model="lazyEnabledDetailComponents" group="componentSettings">
                        <div v-for="component in lazyEnabledDetailComponents" :key="component.name">
                          <detail-item-setting-entry :setting="component" />
                        </div>
                      </draggable>
                    </v-card>
                  </v-col>
                  <v-col cols="6">
                    <v-card rounded color="#ad145766" class="pa-2">
                      <v-card-title>{{ $t('setting.dialog.detailArrangement.disabled') }}</v-card-title>
                      <draggable
                        v-model="lazyDisabledDetailComponents"
                        group="componentSettings"
                        style="min-height: 160px"
                      >
                        <div v-for="component in lazyDisabledDetailComponents" :key="component.name">
                          <detail-item-setting-entry :setting="component" />
                        </div>
                      </draggable>
                    </v-card>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </form>
        </validation-observer>
      </v-card-text>
      <v-card-actions>
        <div class="d-flex flex-row justify-end" style="width: 100%">
          <click-helper @click="onApply">
            <v-btn color="primary" class="mr-1">
              {{ $t('general.dialog.apply') }}
            </v-btn>
          </click-helper>
          <click-helper @click="showSettingDialog = false">
            <v-btn color="default" text>
              {{ $t('general.dialog.cancel') }}
            </v-btn>
          </click-helper>
        </div>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import DataUtil from '@/utils/DataUtil'
import { mapGetters, mapMutations, mapState } from 'vuex'
import { cloneDeep } from 'lodash'
import ClickHelper from '@/components/basic/ClickHelper'
import { required, min_value, max_value } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'
import i18n from '@/i18n'
import draggable from 'vuedraggable'
import DetailItemSettingEntry from '@/components/DetailItemSettingEntry'

setInteractionMode('eager')

extend('required', {
  ...required,
  message: i18n.t('setting.error.required'),
})
extend('max_value', {
  ...max_value,
  message: i18n.t('setting.error.maxValue'),
})

extend('min_value', {
  ...min_value,
  message: i18n.t('setting.error.minValue'),
})

export default {
  name: 'FishSettingDialog',
  components: { DetailItemSettingEntry, ClickHelper, ValidationObserver, ValidationProvider, draggable },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    NOTIFICATION_SOUNDS: DataUtil.NOTIFICATION_SOUNDS,
    lazyOpacity: undefined,
    lazyNotificationSetting: {},
    lazyEnabledDetailComponents: [],
    lazyDisabledDetailComponents: [],
  }),
  computed: {
    showSettingDialog: {
      get() {
        return this.value
      },
      set(show) {
        this.$emit('input', show)
      },
    },
    isMobile() {
      return this.$vuetify.breakpoint.mobile
    },
    ...mapState(['sounds']),
    ...mapGetters(['opacity', 'notification', 'detailComponents']),
  },
  watch: {
    showSettingDialog(showSettingDialog) {
      if (showSettingDialog) {
        this.init()
      }
    },
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.$refs.observer?.reset()
      this.lazyOpacity = this.opacity
      this.lazyNotificationSetting = cloneDeep(this.notification)
      this.lazyEnabledDetailComponents = cloneDeep(this.detailComponents.filter(it => it.enabled))
      this.lazyDisabledDetailComponents = cloneDeep(this.detailComponents.filter(it => !it.enabled))
    },
    playSound(key) {
      this.sounds[key]?.player.volume(this.lazyNotificationSetting.volume).play()
    },
    onApply() {
      this.$refs.observer.validate().then(valid => {
        if (valid) {
          this.apply()
          this.showSettingDialog = false
        }
      })
    },
    apply() {
      this.setOpacity(this.lazyOpacity)
      this.setNotificationSetting(cloneDeep(this.lazyNotificationSetting))
      this.setDetailArrangement(
        cloneDeep({
          components: this.lazyEnabledDetailComponents
            .map(it => ({
              ...it,
              enabled: true,
            }))
            .concat(
              this.lazyDisabledDetailComponents.map(it => ({
                ...it,
                enabled: false,
              }))
            )
            .map((it, index) => {
              return {
                ...it,
                order: index,
              }
            }),
        })
      )
    },
    setNotificationSetting(setting) {
      this.setNotification(setting)
    },
    ...mapMutations(['setOpacity', 'setNotification', 'setDetailArrangement']),
  },
}
</script>

<style scoped></style>