<template>
  <v-dialog v-model="dialog" :fullscreen="isMobile" max-width="1264px" style="z-index: 9998" scrollable>
    <v-card>
      <v-card-title>
        <span class="headline">{{ $t('search.dialog.title') }}</span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row v-if="isMobile">
            <v-col>
              <v-icon class="mr-1">mdi-information</v-icon>
              ACTACTWS
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-autocomplete
                v-model="fishId"
                :items="fishSearchData"
                item-value="id"
                item-text="name"
                :label="$t('search.dialog.placeholder')"
                clearable
                :filter="filterOptions"
              >
                <template v-slot:item="data">
                  <click-helper>
                    <div class="d-flex">
                      <v-list-item-avatar>
                        <div :class="data.item.icon" />
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title v-html="data.item.name"></v-list-item-title>
                      </v-list-item-content>
                    </div>
                  </click-helper>
                </template>
              </v-autocomplete>
            </v-col>
          </v-row>
        </v-container>
        <template v-if="fish != null">
          <v-divider class="mb-3" />
          <div style="position:relative;" class="py-4 px-6">
            <fish-list-expanded-header :value="fish" :fish-time-part="fishListTimePart[fish._id]" />
          </div>
          <fish-list-item-content
            :open="dialog"
            :value="fish"
            :fish-time-part="fishListTimePart[fish._id]"
            :fish-weather-change-part="fishListWeatherChangePart[fish._id]"
            :predators="getPredators(fish)"
          ></fish-list-item-content>
        </template>
      </v-card-text>
      <v-card-actions>
        <click-helper @click="dialog = false" block>
          <v-btn color="default" block text>{{ $t('search.dialog.close') }}</v-btn>
        </click-helper>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import DataUtil from '@/utils/DataUtil'
import FishListItemContent from '@/components/FishListItemContent'
import { mapGetters, mapState } from 'vuex'
import FishListExpandedHeader from '@/components/FishListExpandedHeader'
import * as PinyinMatch from 'pinyin-match'
import ClickHelper from '@/components/basic/ClickHelper'

export default {
  name: 'FishSearch',
  components: { ClickHelper, FishListExpandedHeader, FishListItemContent },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    fishData: {
      type: Array,
      default: () => [],
    },
    fishListTimePart: {
      type: Object,
      default: () => ({}),
    },
    fishListWeatherChangePart: {
      type: Object,
      default: () => ({}),
    },
  },
  data: () => ({
    fishId: undefined,
  }),
  computed: {
    dialog: {
      get() {
        return this.value
      },
      set(showDialog) {
        return this.$emit('input', showDialog)
      },
    },
    fish() {
      return this.fishData.filter(it => it._id === this.fishId)[0]
    },
    fishSearchData() {
      return this.fishData.map(it => ({
        id: it._id,
        name: this.getItemName(it._id),
        icon: this.getItemIconClass(it._id),
      }))
    },
    getPredators() {
      return value =>
        DataUtil.getPredators(
          value,
          this.allFish,
          this.fishListTimePart,
          this.fishListWeatherChangePart
          // this.getFishCompleted(value._id)
        )
    },
    isMobile() {
      return this.$vuetify.breakpoint.mobile
    },
    ...mapState({ allFish: 'fish' }),
    ...mapGetters(['getItemName', 'getFishCompleted', 'getItemIconClass']),
  },
  watch: {
    dialog() {
      this.fishId = undefined
    },
  },
  methods: {
    filterOptions(item, queryText, itemText) {
      if (this.$i18n.locale === 'chs') {
        // console.log(PinyinMatch.match(itemText, queryText))
        return PinyinMatch.match(itemText, queryText) !== false
      } else {
        return itemText.toLowerCase().indexOf(queryText.toLowerCase()) > -1
      }
    },
  },
}
</script>

<style scoped></style>