<template>
  <v-dialog v-model="value" persistent max-width="290" scrollable>
    <template v-slot:activator="{ on, attrs }">
      <click-helper v-on="on" v-bind="attrs">
        <v-btn color="tertiary" block>{{ $t('importExport.dialog.reset.btn') }}</v-btn>
      </click-helper>
    </template>
    <v-card>
      <v-card-title class="headline">
        {{ $t('importExport.dialog.reset.title') }}
      </v-card-title>
      <v-card-actions>
        <v-spacer></v-spacer>
        <click-helper @click="value = false">
          <v-btn color="default" text>
            {{ $t('importExport.dialog.reset.cancel') }}
          </v-btn>
        </click-helper>
        <click-helper @click="onReset">
          <v-btn color="tertiary" text>
            {{ $t('importExport.dialog.reset.confirm') }}
          </v-btn>
        </click-helper>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapMutations } from 'vuex'
import ClickHelper from '@/components/basic/ClickHelper'

export default {
  name: 'ResetButton',
  components: { ClickHelper },
  // props: {
  //   value: {
  //     type: Boolean,
  //     default: false,
  //   },
  // },
  data: () => ({
    value: false,
  }),
  methods: {
    onReset() {
      this.setUserDataToDefault()
      this.value = false
      this.showSnackbar({ text: this.$t('importExport.dialog.message.resetSuccess'), color: 'success' })
    },
    ...mapMutations(['showSnackbar', 'setUserDataToDefault']),
  },
}
</script>

<style scoped></style>
